






import Vue from "vue";
import Component,{mixins} from "vue-class-component";

const ThumbProps = Vue.extend({
    props:{
        image: {},
        index: {},
        activeImage : {}
    }
})

@Component
export default class Thumb extends mixins(ThumbProps) {
    setActive(): void {
        this.$emit("set_active", this.index);
    }
}
